'use client';

import React, { useState, useEffect } from 'react';

const initData = [
  {
    id: 1,
    text: '買牛奶',
    completed: false,
  },
  {
    id: 2,
    text: '繳水電費',
    completed: false,
  },
]

interface Todo {
    id: number;
    text: string;
    completed: boolean;
}

export default function TodoAppPage() {
  const [todos, setTodos] = useState<Todo[]>(initData);
  const [inputText, setInputText] = useState('');
  const [filterStatus, setFilterStatus] = useState<
    'all' | 'active' | 'completed'
  >('all');

  const onAdd = (todo: Todo) => {
    const nextTodos = [todo, ...todos];
    setTodos(nextTodos);
  }

  const onToggleChecked = (todoId: number) => {
    const nextTodos = todos.map((v) => {
      if (v.id === todoId) {
        return { ...v, completed: !v.completed };
      } else {
        return v;
      }
    });

    setTodos(nextTodos);
  }

  const onRemove = (todoId: number) => {
    const nextTodos = todos.filter((v) => {
      return v.id !== todoId;
    });

    setTodos(nextTodos);
  }

    const getFilterTodos = () => {
    switch (filterStatus) {
      case 'active':
        return todos.filter((v) => v.completed === false);
      case 'completed':
        return todos.filter((v) => v.completed === true);
      default: // 相當於'all
        return todos;
    }
  };

  return (
    <>
      <h1>待辦事項</h1>
      <hr />
      <input 
        type="text" 
        value = {inputText}
        onChange = {(e) => {
          setInputText(e.target.value);
        }}
        onKeyDown={(e) => {
          if (e.key === 'Enter') {
            const newId = Number(new Date());
            const newTodo = {
              id: newId,
              text: inputText,
              completed: false,
            }
            onAdd(newTodo);
            setInputText('');
          }
        }}
        />
      <hr />
      <ul>
        {getFilterTodos().map((todo) => {
          return <li key={todo.id}>
            <input 
              type="checkbox"
              checked={todo.completed}
              onChange={(e) => {
                onToggleChecked(todo.id)
              }}
           />
           <span
             style={{
              color: todo.completed ? 'grey' : 'green',
              textDecoration: todo.completed ? 'line-through' : 'none',
             }}
           >
              {todo.text}
           </span>
           <button
            onClick={() => {
              onRemove(todo.id);
            }}
           >
            刪除
           </button>
          </li>
        })}
      </ul>
            <button
        disabled={filterStatus === 'all'}
        onClick={() => {
          setFilterStatus('all');
        }}
      >
        全部
      </button>
      <button
        disabled={filterStatus === 'active'}
        onClick={() => {
          setFilterStatus('active');
        }}
      >
        進行中
      </button>
      <button
        disabled={filterStatus === 'completed'}
        onClick={() => {
          setFilterStatus('completed');
        }}
      >
        已完成
      </button>
    </>
  );
}
